* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  
  body {
    /* background-color: #00000082;
    color: #fff; */
    min-height: 100vh;
    font-family: 'Roboto', 'Oxygen',
      'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
      sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
  
  .Memo3 {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    padding: 1rem;
  }
  
  h1 {
    margin-bottom: 1rem;
  }
  
  button {
    min-width: 50px;
    padding: 1em;
    margin: 0.25em;
    border-radius: 0.5em;
  }
  
  input {
    font-size: 1rem;
    padding: 0.25rem;
    border-radius: 0.5em;
    margin-bottom: 1rem;
    outline: none;
    color: red;
  }

  .Memo2{
    color: red !important;
  }